import {
	BASE_URL
} from '@/network/config'

export default class Upload {
	constructor(object) {
		this.obj = {
			count: 1,
		}
		if (Object.prototype.toString.call(object) === "[object Object]") {
			Object.assign(this.obj, object);
		} else {
			uni.showToast({
				title: '',
				icon: "icon",
				duration: 2000
			});
		}


		return this;
	}
	//  
	async uploadPic() {
		let chooseImageResult = await this.chooseImage()
		console.log("", chooseImageResult)

		let imgArr = await chooseImageResult.tempFilePaths.map(async (item, index) => {
			uni.showLoading({
				title: ',...',
				mask: true
			});
			let uploadFileResult = await this.uploadFile(item)
			return uploadFileResult
		})

		return new Promise((resolve, reject) => {
			Promise.all(imgArr).then((result) => {

				// uni.hideLoading();
				uni.showToast({
					title: '',
					icon: "none",
					duration: 2000
				});
				resolve(result)
			})
		})
	}
	uploadFile(file) {
		return new Promise((resolve, reject) => {
			uni.uploadFile({
				url: BASE_URL + this.obj.url,
				filePath: file,
				name: 'image',
				success: function(res) {
					var data = res.data;
					resolve(JSON.parse(data))
				},
				header: {
					'content-type': 'multipart/form-data'
				},
				formData: {
					folder: 1
				},
				fail: function(res) {
					reject("")
				},
				complete: function(res) {
					uni.hideToast();
				}
			})
		})
	}
	chooseImage() {
		return new Promise((resolve, reject) => {
			uni.showActionSheet({
				itemList: ['', ''],
				itemColor: '#000000',
				success: (status) => {
					if (!status.cancel) {
						let Type = status.tapIndex == 0 ? 'album' : 'camera'
						uni.chooseImage({
							count: this.obj.count, //1, // 9
							sizeType: ['compressed'], //['original', 'compressed'], // 
							sourceType: [Type], //['album','camera'], // 
							success: function(res) {
								resolve(res)
							},
							fail: function() {
								reject("")
							}
						})
					}
				}
			})

		})
	}
}
