import request from './request'

// 
export const getUserInfo = (id) => {
	return request({
		url: 'Personal/members_info',
		mode: 'spin',
		data: {
			m_id : id || ''
		}
	})
}


// 
export const getVerify = (phone) => {
	return request({
		url: 'Login/getVerify',
		data: {
			account: phone || '',
			unique_code: 'login'
		}
	})
}

//  
export const taskVerify = (option) => {
	return request({
		url: 'Login/checkVerify',
		data: {
			m_id: option.m_id || '',
			account: option.phone || '',
			verify: option.verify || '',
			unique_code: 'login'
		}
	})
}


